.footer{
    position: relative;
    z-index: 12;
    width: 70%;
    margin: 2rem auto;
    padding-bottom: 3rem;
    color: #888;
    > *{
        margin: 1rem 0;
    }
    p{
        font-size: 1.5rem;
    }
    .netflix{
        display: grid;
        grid-template-columns: repeat(4,1fr);
        @include tablet{
            grid-template-columns: repeat(2,1fr);
        }
        @include mobile{
            grid-template-columns: repeat(2,1fr);
        }
        li{
            padding: 10px 0;
        }
        a{
            cursor: pointer;
            text-decoration: underline;
        }
    }
    .myproject{
        padding-top: 1rem;
        font-size: 1.3rem;
        *{
            text-align: right;
        }
    }
}